.tableContainer {
  display: grid;
}

.tableHeader {
  display: flex;
  border: 1px solid var(--black);
  font-weight: 600;
}

.img,
.name,
.sizeType,
.size,
.price,
.action {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 5px;
}

.img,
.name,
.sizeType,
.size,
.price {
  border-right: 1px solid var(--black);
}

.img {
  min-width: 150px;
}

.name {
  min-width: 500px;
}

.sizeType {
  min-width: 80px;
}

.size {
  min-width: 70px;
}
.price {
  min-width: 70px;
}

.action {
  min-width: 300px;
}
